.certifications {
  text-align: center;
  padding: 60px 20px;
  background-color: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.certifications h2 {
  font-size: 36px;
  margin-bottom: 50px;
  color: #333;
}

.certifications-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.cert-card {
  background-color: #fff;
  width: 300px;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  text-align: left;
}

.cert-card:hover {
  transform: translateY(-10px);
}

.cert-card h3 {
  font-size: 20px;
  margin-bottom: 10px;
  color: #222;
}

.cert-card p {
  font-size: 16px;
  color: #666;
  margin-bottom: 15px;
}

.cert-card a {
  display: inline-block;
  padding: 10px 15px;
  background-color: #0077b6;
  color: white;
  border-radius: 6px;
  text-decoration: none;
  font-size: 14px;
}

.cert-card a:hover {
  background-color: #005f8a;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .certifications {
    padding: 40px 15px;
  }
  
  .certifications h2 {
    font-size: 30px;
    margin-bottom: 40px;
  }
  
  .certifications-grid {
    gap: 15px;
  }
  
  .cert-card {
    width: calc(50% - 15px);
    min-width: 250px;
  }
}

@media screen and (max-width: 480px) {
  .certifications {
    padding: 30px 10px;
  }
  
  .certifications h2 {
    font-size: 26px;
    margin-bottom: 30px;
  }
  
  .certifications-grid {
    gap: 10px;
  }
  
  .cert-card {
    width: 100%;
    min-width: auto;
  }
  
  .cert-card h3 {
    font-size: 18px;
  }
  
  .cert-card p {
    font-size: 14px;
  }
  
  .cert-card a {
    padding: 8px 12px;
    font-size: 13px;
  }
}